body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container h1 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 0.1rem;
}


.but {
  border:none;
  /* font-size: 2rem; */
  align-items: center;
  box-shadow: 
    12px 12px 16px  rgba(0, 0, 0, 0.25),
    -8px -8px 12px  rgba(255, 255, 255, 0.3);
  border-radius: 15vh;
  display: flex;
  flex-direction: column;
  height: 30vh;
  justify-content: flex-end;
  width: 30vh;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 3vh;
  margin-left: 1vw;
  margin-right: 1vw;


}


.change-time-button{
  border:none;
  margin: 2px;
  padding: 5px;
  height: 10vh;
  width: 5vh;
  font-size: 2rem;
  cursor: pointer;
}

.but h1{
  font-size:2rem;
  margin:0;
}

.other-time-button{
  border:none;
  background-color: inherit ;
  padding-bottom:5vh;
  font-size: 1rem;
}


.other-time-button:focus{
  border:none;
    }


.but h3{
margin:0;
  font-size:1.5rem;
}

.but:focus {
  border:none;

  box-shadow: 
  3px 3px 16px  rgba(0, 0, 0, 0.25),
  2px 2px 1px  rgba(255, 255, 255, 0.3);

}

.container {
  height:100vh;
  /* padding-top: 45px; */
  align-items: center;
  width:100vw;
  display:flex;
  flex-direction: column;
  justify-content:center;
} 

.container-green{
  background-color: #b7d783;

}

.container-green .but{
  background-color: #afd275;
  color: #46542f;
}

.container-blue .but{
  background-color: #77cad4 ;
  color: #18282a;

}


.container-green .change-time-button{
  color: #46542f;
  background-color: inherit ;

}

.container-blue .change-time-button{
  background-color: inherit ;
  color: #18282a;

}

.hidden{
  visibility: hidden !important;
}



.container-blue h1 {
  color: #305155;

}

.container-blue :is( h3, .update_button) {
  color: #47797f;

}



.container-green h1 {
  color: #343f23;

}

.container-green :is(h3, .update_button) {
  color: #697e46;

}

.update_button{
  border:none;
  background-color: inherit;
}

.update_button:focus{
  border:none;
}
.container h3 {
  margin-bottom: 10vh;
  margin-top:0;
}

.container-blue {
  background-color: #85cfd8;

}


.container-blue .loader {
  fill: #47797f;

}

.container-green .loader {
  fill: #697e46;

}


.button-grid{
  display: grid;
  grid-template-columns: 10% 80% 10%;
}

.central-button-flex{
  display: flex;
  align-items: center;
  justify-content: center;
}

.set-time-flex{
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
}